#div-form-task {
	background-color: #fff;
	display: flex;
	flex-direction: column;
	height: fit-content;
}

#div-form-task > * {
	margin-bottom: 10px;
}
#div-form-task h2 {
	text-align: center;
	text-transform: capitalize;
}

#div-form-task label {
	font-size: 1.2rem;
}

#div-form-task input {
	font-size: 1.2rem;
}

#div-form-task button {
	font-size: 1rem;
	padding: 5px;
	color: white;
	border: 0;
	border-radius: 5px;
	cursor: pointer;
}

#btn-add-task {
	background-color: #ddaf94;
	margin-right: 5px;
}

#btn-clear {
	background-color: #a3bcb6;
}

#div-form-task small {
	color: red;
}
