td {
	font-size: 1.2rem;
}

td button {
	outline: 0;
	border: 0;
	cursor: pointer;
	background: none;
	font-size: 1.2rem;
	margin-left: 10px;
}
td .bullet {
	color: #ddaf94;
	margin-right: 10px;
}
td i.fa-check-circle {
	color: green;
}

td i.fa-times-circle {
	color: red;
}

.td-pending-first-col {
	width: 75%;
}

.td-pending-second-col {
	width: 25%;
}
