* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	background-color: #a3bcb6;
	color: #3c403d;
}

button {
	cursor: pointer;
}

button:hover {
	opacity: 0.8;
}

#div-app {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-between;
	width: 80%;
	margin: 40px auto;
}

#div-app > div {
	width: 32%;
	border-radius: 10px;
	padding: 20px 40px;
	box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.1);
	overflow: hidden;
}

#div-table-pending-tasks table tr td:nth-of-type(1) {
	text-align: left;
}

#div-app #div-table-pending-tasks {
	background-color: #daded4;
}

#div-app #div-table-done-tasks {
	background-color: #e8cebf;
}
